bezahlungen-view{margin:0 1em;display:block;margin-bottom:3em}bezahlungen-group{display:block;-webkit-animation:group-open .3s cubic-bezier(0, 0, 0.2, 1);animation:group-open .3s cubic-bezier(0, 0, 0.2, 1);will-change:transform,opacity}@-webkit-keyframes group-open{0%{opacity:.6;transform:translateY(0.4em)}100%{opacity:1;transform:translateY(0)}}@keyframes group-open{0%{opacity:.6;transform:translateY(0.4em)}100%{opacity:1;transform:translateY(0)}}.bezahlungen-group__title{font-size:1em;font-weight:normal;color:#6200ee;-webkit-margin-before:.4em;margin-block-start:.4em;-webkit-margin-after:.4em;margin-block-end:.4em}.bezahlungen-group__container{display:grid;grid-template-columns:repeat(auto-fill, minmax(20em, 1fr))}.bezahlungen-group__container>bezahlung-card{margin:.2em}